import React from "react";
import './InfoCovid.css';

export default class InfoCovid extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            dataCovid:{
                distanciationPhysique: null,
                portDuMasque: null,
                gestesBarrieres: null,
                brassageUsagers: null,
                nettoyageDesinfection: null,
                communicationFormation: null,
            },
        }
        this.handleChange = this.handleChange.bind(this)
    }

    handleChange(event) {
        if (event.target.name in this.state.dataCovid) {
            let newDataCovid = this.state.dataCovid;
            newDataCovid[event.target.name] = event.target.value;
            this.setState({dataCovid: newDataCovid})
            this.props.onChange(newDataCovid);
        }
    }

    render() {
        return(
            <div id={"InfoCovid-container"}>
                <h1 className={"PLP-questions"} id={"PLP-CovidDisclaimer"}>
                    Dans le cader de la pandémie du COVID-19, une circulaire est apparue
                    le 11 juin 2020 et détaille les différentes consignes sanitaires à respecterlors des événements. Qu’allez-vous mettre en place pour faire respecter
                    ces différents points ?
                </h1>
                <div id={"PLP-distanciationPhys"}>
                    <h2 className={"PLP-questions"}> Respect de la distanciation sociale </h2>
                    <textarea
                        className={"PLP-retours PLP-textarea"}
                        name={"distanciationPhysique"}
                        rows={"3"}
                        cols={"40"}
                        maxLength={"300"}
                        onChange={this.handleChange}
                        //placeholder={"Si votre description est trop longue fournissez une annexe"}
                        value={this.state.dataCovid.distanciationPhysique}
                    />
                </div>
                <div id={"PLP-portMasque"}>
                    <h2 className={"PLP-questions"}> Respect du port systématique du masque par tous si les règles de distanciation physique ne peuvent être garanties </h2>
                    <textarea
                        className={"PLP-retours PLP-textarea"}
                        name={"portDuMasque"}
                        rows={"3"}
                        cols={"40"}
                        maxLength={"300"}
                        onChange={this.handleChange}
                        //placeholder={"Si votre description est trop longue fournissez une annexe"}
                        value={this.state.dataCovid.portDuMasque}
                    />
                </div>
                <div id={"PLP-gestesBarrieres"}>
                    <h2 className={"PLP-questions"}> Application des gestes barrières </h2>
                    <textarea
                        className={"PLP-retours PLP-textarea"}
                        name={"gestesBarrieres"}
                        rows={"3"}
                        cols={"40"}
                        maxLength={"300"}
                        onChange={this.handleChange}
                        //placeholder={"Si votre description est trop longue fournissez une annexe"}
                        value={this.state.dataCovid.gestesBarrieres}
                    />
                </div>
                <div id={"PLP-brassageUsagers"}>
                    <h2 className={"PLP-questions"}> Limitation du brassage des usagers </h2>
                    <textarea
                        className={"PLP-retours PLP-textarea"}
                        name={"brassageUsagers"}
                        rows={"3"}
                        cols={"40"}
                        maxLength={"300"}
                        onChange={this.handleChange}
                        //placeholder={"Si votre description est trop longue fournissez une annexe"}
                        value={this.state.dataCovid.brassageUsagers}
                    />
                </div>
                <div id={"PLP-nettoyageDesinfection"}>
                    <h2 className={"PLP-questions"}> Nettoyage et désinfection des locaux et matériels </h2>
                    <textarea
                        className={"PLP-retours PLP-textarea"}
                        name={"nettoyageDesinfection"}
                        rows={"3"}
                        cols={"40"}
                        maxLength={"300"}
                        onChange={this.handleChange}
                        //placeholder={"Si votre description est trop longue fournissez une annexe"}
                        value={this.state.dataCovid.nettoyageDesinfection}
                    />
                </div>
                <div id={"PLP-communicationFormation"}>
                    <h2 className={"PLP-questions"}> Communication et formation apportée aux agents et aux usagers </h2>
                    <textarea
                        className={"PLP-retours PLP-textarea"}
                        name={"communicationFormation"}
                        rows={"3"}
                        cols={"40"}
                        maxLength={"300"}
                        onChange={this.handleChange}
                        //placeholder={"Si votre description est trop longue fournissez une annexe"}
                        value={this.state.dataCovid.communicationFormation}
                    />
                </div>
            </div>
        )
    }
}