import React from 'react';
import "./ManifestationAssociative.css"
import InfoGeneral from "../Components/ManifestationAssociative/InfoGeneral";
import InfoCovid from "../Components/ManifestationAssociative/InfoCovid";

export default class ManifestationAssociative extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            surCampus : null,
            dataGeneral: {
                dateDepot: null,
                dateDebutDeManif: null,
                heureDebutDeManif: null,
                dateFinDeManif: null,
                heureFinDeManif: null,
                associationOrganisatrice: null,
                nomManif: null,
                presidentAssoNom: null,
                presidentAssoPrenom: null,
                presidentAssoTel: null,
                presidentAssoMail: null,
                contactSpecifiqueNom: null,
                contactSpecifiquePrenom: null,
                contactSpecifiqueTel: null,
                contactSpecifiqueMail: null,
                descriptionManif: null,
                etatDesLieux: null,
                },
            dataSurCampus: {
                    activite: null,
                    locaux: null,
                    materielsElec: null,
                    appareil: null,
                    mobiliers: null,
                    communication: null,
                    risquesMat: null,
                    risquesHumains: null,
                    reamenagementSpecifiqueOuiNon:null,
                    reamenagementSpecifiquePlan: null,
                    utilisationExceptionnelleLocaux: null,
                    autorisations: null,
                    pleinAirOuiNon: null,
                    pleinAirPlanB: null,
                },
            dataHorsCampus: {
                    activite: null,
                    lieu: null,
                    autorisations: null,
                    materielEcole: null,
                    communication: null,
                    risquesMat: null,
                    risquesHumains: null,
                    assurance: null
                },
            dataCovid:{
                    distanciationPhysique: null,
                    portDuMasque: null,
                    gestesBarrieres: null,
                    brassageUsagers: null,
                    nettoyageDesinfection: null,
                    communicationFormation: null,
                },
            Annexes:null,
        }

        this.handleChangeGeneral = this.handleChangeGeneral.bind(this)
        this.handleChangeCovid = this.handleChangeCovid.bind(this)
    }

    handleChangeGeneral(event) {
        if ("surCampusOui" === event){
            this.setState({surCampus: true})
        }
        else if (event === "surCampusNon"){
            this.setState({surCampus: false})
        }
        else {
            this.setState({dataGeneral: event})
        }
        //console.log(this.state.dataGeneral);
        //console.log(this.state.surCampus);
    }
    handleChangeCovid(event){
        this.setState({dataCovid: event})
    }

    render() {
        return(
            <section id="ManifestationAssociative-Container">
                <form>
                    <InfoGeneral onChange={this.handleChangeGeneral}/>
                    <InfoCovid />
                </form>
            </section>
        )
    }

    componentDidMount() {
        let ulMenuNavigation = document.getElementById("ulMenuNavigation");
        ulMenuNavigation.style.backgroundColor = "rgba(19, 67, 91, 1)";
    }

    componentWillUnmount() {
        let ulMenuNavigation = document.getElementById("ulMenuNavigation");
        ulMenuNavigation.style.backgroundColor = "rgba(19, 67, 91, 0)";
    }
}