import React from "react";
import './InfoGeneral.css'

export default class InfoGeneral extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            surCampus: null,
            dataGeneral: {
                dateDepot: null,
                dateDebutDeManif: null,
                heureDebutDeManif: null,
                dateFinDeManif: null,
                heureFinDeManif: null,
                associationOrganisatrice: null,
                nomManif: null,
                presidentAssoNom: null,
                presidentAssoPrenom: null,
                presidentAssoTel: null,
                presidentAssoMail: null,
                contactSpecifiqueNom: null,
                contactSpecifiquePrenom: null,
                contactSpecifiqueTel: null,
                contactSpecifiqueMail: null,
                descriptionManif: null,
                etatDesLieux: null,
            },
        }

        this.styleOuiNon_actif = {
            backgroundColor: "#13435b",
            color: "white",
            borderStyle: "double",
            borderWidth: "0.3em",
            borderColor: "white",
        }
        this.styleOuiNon_inactif = {
            backgroundColor: "white",
            color: "#13435b",
            borderStyle: "solid",
            borderWidth: "0.05em",
            borderColor: "#13435b",
        }

        this.handleChange = this.handleChange.bind(this)
        this.surCampusOui = this.surCampusOui.bind(this)
        this.surCampusNon = this.surCampusNon.bind(this)
    }

    handleChange(event) {
        if (event.target.name in this.state.dataGeneral) {
            let newDataGeneral = this.state.dataGeneral;
            newDataGeneral[event.target.name] = event.target.value;
            this.setState({dataGeneral: newDataGeneral})
            this.props.onChange(newDataGeneral);
        }
        //console.log(event.target.name);
        //console.log(this.state.dataGeneral);
        //console.log(this.state.surCampus);
    }

    surCampusOui () {
        this.setState({surCampus: true});
        let PLPSurCampus = document.getElementById("PLP-SurCampus")
        let PLPHorsCampus = document.getElementById("PLP-HorsCampus")
        for (let s in this.styleOuiNon_actif){PLPSurCampus.style[s] = this.styleOuiNon_actif[s]}
        for (let s in this.styleOuiNon_inactif){PLPHorsCampus.style[s] = this.styleOuiNon_inactif[s]}
        this.props.onChange("surCampusOui");
    }
    surCampusNon () {
        this.setState({surCampus: false});
        let PLPSurCampus = document.getElementById("PLP-SurCampus")
        let PLPHorsCampus = document.getElementById("PLP-HorsCampus")
        for (let s in this.styleOuiNon_actif){PLPHorsCampus.style[s] = this.styleOuiNon_actif[s]}
        for (let s in this.styleOuiNon_inactif){PLPSurCampus.style[s] = this.styleOuiNon_inactif[s]}
        this.props.onChange("surCampusNon");
    }

    render() {
        return(
            <div id={"InfoGeneral-container"}>
                <div id={"PLP-surCampusOuNon"}>
                    <h1 className={"PLP-questions"} id={"PLP-surCampus-question"}> Où se déroule votre événement ? </h1>
                    <div id={"PLP-SurCampus"} onClick={() => {this.surCampusOui()}}> SUR CAMPUS </div>
                    <div id={"PLP-HorsCampus"} onClick={() => {this.surCampusNon()}}> HORS CAMPUS </div>
                </div>
                <div id={"PLP-Dates"}>
                    <div className={"PLP-Dates-containers"}>
                        <h1 className={"PLP-questions"} id={"PLP-DateDepotQuestion"}> Date de dépôt de la demande : </h1>
                        <input className={"PLP-retours"} name={"dateDepot"} value={this.state.dataGeneral.dateDepot} onChange={this.handleChange}/>
                    </div>
                    <div className={"PLP-Dates-containers"}>
                        <h1 className={"PLP-questions"} id={"PLP-DateDepotQuestion"}> Date et heure de début de la manifestation : </h1>
                        <input
                            className={"PLP-retours"}
                            name={"dateDebutDeManif"}
                            onChange={this.handleChange}
                            value={this.state.dataGeneral.dateDebutDeManif}
                        />
                        <input
                            className={"PLP-retours"}
                            name={"heureDebutDeManif"}
                            onChange={this.handleChange}
                            value={this.state.dataGeneral.heureDebutDeManif}
                        />
                    </div>
                    <div className={"PLP-Dates-containers"}>
                        <h1 className={"PLP-questions"} id={"PLP-DateDepotQuestion"}> Date et heure de fin de la manifestation : </h1>
                        <input
                            className={"PLP-retours"}
                            name={"dateFinDeManif"}
                            onChange={this.handleChange}
                            value={this.state.dataGeneral.dateFinDeManif}
                        />
                        <input
                            className={"PLP-retours"}
                            name={"heureFinDeManif"}
                            onChange={this.handleChange}
                            value={this.state.dataGeneral.heureFinDeManif}
                        />
                    </div>
                </div>
                <div className={"PLP-asso-manif"}>
                    <h1 className={"PLP-questions"} id={"PLP-associationOrganisatrice"}> Association organisatrice : </h1>
                    <input
                        className={"PLP-retours PLP-retours-asso-manif"}
                        name={"associationOrganisatrice"}
                        onChange={this.handleChange}
                        value={this.state.dataGeneral.associationOrganisatrice}
                    />
                </div>
                <div className={"PLP-asso-manif"}>
                    <h1 className={"PLP-questions"} id={"PLP-nomManif"}> Nom de la manifestation : </h1>
                    <input
                        className={"PLP-retours PLP-retours-asso-manif"}
                        name={"nomManif"}
                        onChange={this.handleChange}
                        value={this.state.dataGeneral.nomManif}
                    />
                </div>
                <div id={"PLP-prez-contact"}>
                    <div className={'PLP-contact-container'}>
                        <h1 className={"PLP-questions"} id={"PLP-president-h1"}> Président de l'association </h1>
                        <div className={"PLP-Dates-containers"}>
                            <h2 className={"PLP-questions"}> Nom : </h2>
                            <input
                                className={"PLP-retours"}
                                name={"presidentAssoNom"}
                                onChange={this.handleChange}
                                value={this.state.dataGeneral.presidentAssoNom}
                            />
                        </div>
                        <div className={"PLP-Dates-containers"}>
                            <h2 className={"PLP-questions"}> Prénom : </h2>
                            <input
                                className={"PLP-retours"}
                                name={"presidentAssoPrenom"}
                                onChange={this.handleChange}
                                value={this.state.dataGeneral.presidentAssoPrenom}
                            />
                        </div>
                        <div className={"PLP-Dates-containers"}>
                            <h2 className={"PLP-questions"}> Téléphone : </h2>
                            <input
                                className={"PLP-retours"}
                                name={"presidentAssoTel"}
                                onChange={this.handleChange}
                                value={this.state.dataGeneral.presidentAssoTel}
                            />
                        </div>
                        <div className={"PLP-Dates-containers"}>
                            <h2 className={"PLP-questions"}> Adresse mail : </h2>
                            <input
                                className={"PLP-retours"}
                                name={"presidentAssoMail"}
                                onChange={this.handleChange}
                                value={this.state.dataGeneral.presidentAssoMail}
                            />
                        </div>
                    </div>
                    <div className={'PLP-contact-container'}>
                        <h1 className={"PLP-questions"} id={"PLP-contactSpe-h1"}> Contact spécifique </h1>
                        <div className={"PLP-Dates-containers"}>
                            <h2 className={"PLP-questions"}> Nom : </h2>
                            <input
                                className={"PLP-retours"}
                                name={"contactSpecifiqueNom"}
                                onChange={this.handleChange}
                                value={this.state.dataGeneral.contactSpecifiqueNom}
                            />
                        </div>
                        <div className={"PLP-Dates-containers"}>
                            <h2 className={"PLP-questions"}> Prénom : </h2>
                            <input
                                className={"PLP-retours"}
                                name={"contactSpecifiquePrenom"}
                                onChange={this.handleChange}
                                value={this.state.dataGeneral.contactSpecifiquePrenom}
                            />
                        </div>
                        <div className={"PLP-Dates-containers"}>
                            <h2 className={"PLP-questions"}> Téléphone : </h2>
                            <input
                                className={"PLP-retours"}
                                name={"contactSpecifiqueTel"}
                                onChange={this.handleChange}
                                value={this.state.dataGeneral.contactSpecifiqueTel}
                            />
                        </div>
                        <div className={"PLP-Dates-containers"}>
                            <h2 className={"PLP-questions"}> Adresse mail : </h2>
                            <input
                                className={"PLP-retours"}
                                name={"contactSpecifiqueMail"}
                                onChange={this.handleChange}
                                value={this.state.dataGeneral.contactSpecifiqueMail}
                            />
                        </div>
                    </div>
                </div>
                <div id={"PLP-description"}>
                    <h1 className={"PLP-questions"} id={"PLP-description-h1"}> Description de la manifestation : </h1>
                    <textarea
                        className={"PLP-retours"}
                        id={"PLP-description-textarea"}
                        name={"descriptionManif"}
                        rows={"12"}
                        cols={"40"}
                        maxLength={"750"}
                        onChange={this.handleChange}
                        placeholder={"Si votre description est trop longue fournissez une annexe"}
                        value={this.state.dataGeneral.descriptionManif}
                    />
                </div>
            </div>
        )
    }

    componentDidMount() {
        let PLPSurCampus = document.getElementById("PLP-SurCampus");
        let PLPHorsCampus = document.getElementById("PLP-HorsCampus");
        for (let s in this.styleOuiNon_inactif){
            PLPSurCampus.style[s] = this.styleOuiNon_inactif[s];
            PLPHorsCampus.style[s] = this.styleOuiNon_inactif[s];
        }

    }
}